@import '../../styles/main.scss';

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;

  padding: 0 16px;
  margin-top: auto;

  border-top: 1px solid $colorElements;
  background-color: $colorWhite;

  @include onTablet {
    padding: 0 32px;
    margin-top: 80px;
  }

  @include onDesktop {
    padding: 0 152px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    margin-bottom: 32px;

    @include onTablet {
      flex-direction: row;
      justify-content: space-between;
      height: 96px;
      margin-bottom: 0;
    }
  }

  &__icon {
    display: flex;
    justify-items: center;
    margin: 32px 0;
    width: 89px;
    height: 30px;
  }

  &__list {
    display: flex;
    flex-direction: column;

    @include onTablet {
      display: flex;
      flex-direction: row;
      align-items: center;
      gap: 13.5px;
    }

    @include onDesktop {
      gap: 107px;
    }
  }

  &__item {
    font-family: 'Mont-Bold';
    color: $colorSecondary;
    font-weight: 700;
    font-size: 12px;
    line-height: 11px;

    text-transform: uppercase;
    letter-spacing: 0.04em;

    margin-bottom: 16px;
    transition: color 0.3s;

    &:hover {
      color: $colorPrimary;
      text-shadow: 0px 0px 1px rgba(193, 236, 176, 0.7);
    }

    &:last-child {
      margin-bottom: 0;
    }

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__button {
    font-family: Mont;
    color: $colorSecondary;
    font-weight: 600;
    font-size: 12px;
    border: none;
    background: $colorWhite;

    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    margin-top: 32px;

    @include onTablet {
      margin-top: 0;
    }

    transition: color 0.3s;

    &:hover {
      color: black;
    }
  }

  &__arrow {
    width: 32px;
    height: 32px;
    border: 1px solid #b4bdc4;
    margin-left: 16px;
    transition: box-shadow 0.3s;

    &:hover {
      box-shadow: 0px 3px 13px rgba(23, 32, 49, 0.4);
    }
  }
}
