@import '../../styles/main.scss';

.banner {
  display: flex;
  border-radius: 8px;
  margin: 0 auto;

  @include onTablet {
    width: 592px;
  }

  @include onDesktop {
    width: 1136px;
  }

  &__image {
    @include onMobile {
      width: 100vw;
    }

    @include onTablet {
      height: 190px;
    }

    @include onDesktop {
      height: 400px;
    }
  }

  &__slider {
    margin: 0 auto 18px;
    width: 100%;

    @include onTablet {
      width: 490px;
    }

    @include onDesktop {
      width: 1040px;
    }
  }
}

.prev {
  @include onMobile {
    display: none;
  }

  @include onTablet {
    display: inline-block;
    margin-right: 19px;
    width: 32px;
    height: 189px;
    border-radius: 48px;
    border: 1px solid #b4bdc3;
    cursor: pointer;
  }

  @include onDesktop {
    width: 32px;
    height: 400px;
  }

  transition: border 0.3s;

  &__arrow {
    background-image: url(../../icons/banner-button-prev.svg);
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
  }

  &:hover {
    border: 1px solid #0f0f11;
  }
}

.next {
  @include onMobile {
    display: none;
  }

  @include onTablet {
    display: inline-block;
    margin-left: 19px;
    width: 32px;
    height: 189px;
    border-radius: 48px;
    border: 1px solid #b4bdc3;
    cursor: pointer;
  }

  @include onDesktop {
    width: 32px;
    height: 400px;
  }

  transition: border 0.3s;

  &__arrow {
    background-image: url(../../icons/banner-button-next.svg);
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
  }

  &:hover {
    border: 1px solid #0f0f11;
  }
}

.swiper {
  z-index: 0 !important;
}

.swiper-pagination {
  position: relative !important;
  z-index: 0 !important;
  margin-bottom: 66px !important;

  @include onTablet {
    margin-bottom: 74px !important;
  }

  @include onDesktop {
    margin-bottom: 90px !important;
  }
}

.swiper-pagination-bullet {
  height: 4px !important;
  width: 14px !important;
  border-radius: 0 !important;
  background-color: #0f0f11 !important;
}

.swiper-pagination-bullet-acative {
  background-color: #e2e6e9 !important;
}
